@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
.wrapper {
  width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}
@media (min-width: 768px) {
  .wrapper {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .wrapper {
    width: 1200px;
  }
}
.button {
  font-family: inherit;
  color: #ffffff;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  background-color: #81b3d2;
  border: none;
  text-transform: uppercase;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #e5e5e5;
}
.page {
  height: 100%;
}
.page__body {
  margin: 0;
  min-height: 100%;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  background-color: #f2f2f2;
}
.main-header {
  position: relative;
  background-color: #ffffff;
  min-height: 56px;
}
.main-header__logo {
  position: absolute;
  display: block;
  width: 185px;
  height: 96px;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .main-header__logo {
    z-index: 2;
  }
}
.main-nav {
  position: relative;
}
.main-nav__toggle {
  position: absolute;
  display: block;
  width: 23px;
  height: 22px;
  right: 20px;
  top: 17px;
  border: none;
  cursor: pointer;
  z-index: 2;
  background-color: transparent;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 9px;
  right: -2px;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav__list {
  position: absolute;
  margin: 0;
  padding: 0;
  list-style-type: none;
  width: 100%;
}
.main-nav__list::after {
  position: absolute;
  content: "";
  display: block;
  width: 100%;
  height: 7px;
  bottom: 0;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-nav__list {
    position: static;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
  .main-nav__list::after {
    display: none;
  }
}
.main-nav--closed .main-nav__list {
  display: none;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__list {
    display: grid;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__list {
  display: block;
  position: relative;
  padding-top: 96px;
}
.main-nav__item {
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
}
.main-nav__item:not(:last-child) {
  border-bottom: 1px solid #537387;
}
.main-nav__item:not(:first-child) {
  border-top: 1px solid #9ac2db;
}
@media (min-width: 768px) {
  .main-nav__item:not(:last-child),
  .main-nav__item:not(:first-child) {
    border: none;
  }
  .main-nav__item:nth-child(3),
  .main-nav__item:nth-child(7) {
    grid-column-start: 4;
  }
}
.main-nav__link {
  display: block;
  color: #ffffff;
  background-color: #81b3d2;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-decoration: none;
  padding: 17px 20px 12px;
}
.main-nav__link:hover {
  background-color: #669ec0;
}
.main-nav__link:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .main-nav__link {
    color: #000000;
    background-color: transparent;
    font-size: 14px;
    line-height: 21px;
    padding: 18px 10px 17px;
  }
  .main-nav__link--current {
    color: #766357;
  }
  .main-nav__link:hover {
    color: #81b3d2;
    background-color: transparent;
  }
  .main-nav__link:active {
    background-color: transparent;
    opacity: 0.3;
  }
}
